.Nav{
    position: fixed;
    top : 0px;
    width:100%;
    height : 20px;
    padding : 20px;
    z-index : 1;
    display: flex;
    justify-content: space-between;
    transition-timing-function: ease-in;
    transition: all 0.6s;
}
.Nav-logo{
    position : fixed;
    left:30px;
    object-fit: contain;
    margin-top:-4px;
    width : auto;
    height:40px;
}
.nav_black{
    background-color: black;
}