.banner{
    object-fit :cover;
    height : 448px;
}
.banner_content{
    margin-left :30px;
    padding-top: 140px;
    height:200px;
}
.banner-title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner_desc{
    width : 45rem;
    line-height: 1.3;
    padding-top:1rem;
    font-size:0.8rem;
    max-width:360px;
    height:80px;
}
.button{
    cursor:pointer;
    color : #fff;
    outline:none;
    border:none;
    font-weight:700;
    border-radius:5px;
    padding-left:2rem;
    padding-right:2rem;
    margin-right:1rem;
    padding-top:0.5rem;
    padding-bottom: 0.5rem;
    background-color:rgba(51,51,51,0.5);

}
.button:hover{
    color:#000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}
.banner--fadebottom{
    height:7.4rem;
    background-image:linear-gradient(
        180deg,transparent,
        rgba(36, 33, 33, 0.61),
        #111
    );
    
}
.logo{
    width : auto;
    height:50px;
}